.footer {
    padding: 6rem 0;
    background-color: rgba(19,19,19,0.8);
}
.footer-container {
    max-width: 1140px;
    margin: auto;
    display: flex;
    grid-gap: 40px;
}
.left {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.location {
    display: flex;
    margin-bottom: 0.8rem;
}
.location p { 
    line-height: 30px;
}
.right {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.social {
    margin-top: 1rem;
}
h4 {
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}
p{
    font-size: 1.2rem;
}
